import { Glue42 } from "../../glue";

export async function setupAGM(agm: Glue42.AGM.API, channels: Glue42.Channels.API) {

    let windowId;
    if (typeof window !== "undefined") {
        if (window.glue42gd) {
            windowId = window.glue42gd.windowId;
        }
    }

    if (!windowId) {
        // not supported
        return;
    }
    await agm.register("T42.Channels.Command", (args: any) => {
        const command = args.command;
        if (!command) {
            throw new Error("missing command argument");
        }
        if (command === "join") {
            const id = args.channel;
            if (!id) {
                throw new Error("missing argument id");
            }
            channels.join(id);
            return;
        }
        if (command === "leave") {
            channels.leave();
            return;
        }
        if (command === "get") {
            const id = channels.current();
            return { id };
        }
        throw new Error(`unknown command ${command}`);
    });

    // agm interface is available for SW windows only
    agm.invoke("T42.Channels.Announce", { swId: windowId, instance: agm.instance.instance });
}
