import { Glue42 } from "../../glue";

const CONTEXT_PREFIX = "___channel___";

export class SharedContextSubscriber {
    private unsubscribeFunc: () => void = undefined;
    private current: string = undefined;
    private callback: any;

    constructor(private contexts: Glue42.Contexts.API) {
    }

    public subscribe(callback: any) {
        this.callback = callback;
    }

    public async subscribeFor(name: string, callback: (data: object, context: Glue42.Channels.ChannelContext, updaterId: string) => void): Promise<() => void> {
        if (!await this.isChannel(name)) {
            return Promise.reject(Error(`Channel with id: ${name} doesn't exist !`));
        }
        return this.contexts.subscribe(this.createContextName(name), (data: any, delta: any, removed: any, unsubscribe: any, extraData: any) => {
            callback(data.data, data, extraData && extraData.updaterId);
        });
    }

    public async switchChannel(name: string) {
        this.unsubscribe();
        this.current = name;
        const sharedContextName = this.createContextName(name);
        this.unsubscribeFunc = await this.contexts.subscribe(sharedContextName, (data) => {
            if (this.callback) {
                this.callback(data);
            }
        });
    }

    public unsubscribe(): Promise<void> {
        if (this.unsubscribeFunc) {
            this.unsubscribeFunc();
        }
        return Promise.resolve();
    }

    public async add(id: string, data: Glue42.Channels.ChannelContext): Promise<void> {
        const name = this.createContextName(id);
        return this.contexts.set(name, data);
    }

    public all(): Promise<string[]> {
        const allContexts = this.contexts.all();
        const channels = allContexts.filter((c: string) => c.startsWith(CONTEXT_PREFIX));
        const ids = channels.map((c: string) => c.substr(CONTEXT_PREFIX.length));
        return Promise.resolve(ids);
    }

    public async getContextData(name: string): Promise<Glue42.Channels.ChannelContext> {
        if (!await this.isChannel(name)) {
            return Promise.reject(Error(`A channel with name: ${name} doesn't exist !`));
        }

        return new Promise((resolve, reject) => {
            this.contexts.subscribe(this.createContextName(name), (data) => {
                resolve(data);
            }).then((unsub) => unsub);
        });
    }

    public update(id: string, data: any): Promise<void> {
        const name = this.createContextName(id);
        return this.contexts.update(name, data);
    }

    private createContextName(id: string) {
        return CONTEXT_PREFIX + id;
    }

    private async isChannel(id: string): Promise<boolean> {
        return (await this.all()).some((channelId) => channelId === id);
    }
}
