import { Glue42 } from "../../glue";
import { default as CallbackRegistryFactory, CallbackRegistry } from "callback-registry";
import { SharedContextSubscriber } from "./contexts";

export class ChannelsImpl implements Glue42.Channels.API {
    private subsKey = "subs";
    private changedKey = "changed";

    private registry: CallbackRegistry = CallbackRegistryFactory();
    private currentContext: string;

    constructor(private shared: SharedContextSubscriber) {
        this.shared.subscribe(this.handler.bind(this));
    }

    public current(): string {
        return this.currentContext;
    }

    public subscribe(callback: (data: object, context: Glue42.Channels.ChannelContext, updaterId: string) => void, channelContext?: Glue42.Channels.ChannelContext): () => void {
        return this.registry.add(this.subsKey, callback);
    }

    public async subscribeFor(name: string, callback: (data: object, context: Glue42.Channels.ChannelContext, updateId: string) => void): Promise<() => void> {
        const unsub = await this.shared.subscribeFor(name, callback);
        return unsub;
    }

    public async publish(data: object, channelId?: string): Promise<void> {
        if (channelId) {
            if (typeof channelId !== "string") {
                return Promise.reject(Error("Please provide the channelId as a string !"));
            }

            const context: Glue42.Channels.ChannelContext = await this.get(channelId);

            if (!context.name) {
                return Promise.reject(Error("no name provided")); // TODO: better error
            }

            return this.shared.update(context.name, {data});
        }

        if (!this.currentContext) {
            throw new Error("not joined to any channel");
        }
        return this.shared.update(this.currentContext, {data});
    }

    public join(id: string): Promise<void> {
        this.currentContext = id;
        this.registry.execute(this.changedKey, id);
        return this.shared.switchChannel(id);
    }

    public leave(): Promise<void> {
        this.currentContext = undefined;
        this.registry.execute(this.changedKey, undefined);
        return this.shared.unsubscribe();
    }

    public all(): Promise<string[]> {
        return this.shared.all();
    }

    public get(channelName: string): Promise<Glue42.Channels.ChannelContext> {
        return this.shared.getContextData(channelName);
    }

    public changed(callback: (channel: string) => () => void) {
        this.registry.add(this.changedKey, callback);
    }

    public meta(name: string, meta: object): Promise<void> {
        throw new Error("Method not implemented.");
    }

    public async add(info: Glue42.Channels.ChannelContext): Promise<Glue42.Channels.ChannelContext> {
        if (!info) {
            throw new Error("info argument can not be undefined");
        }

        if (!info.name) {
            throw new Error("info.name is missing");
        }

        const context: Glue42.Channels.ChannelContext = {
            name: info.name,
            meta: info.meta || {},
            data: info.data || {}
        };

        await this.shared.add(info.name, context);
        return context;
    }

    public remove(id: string): void {
        throw new Error("Method not implemented.");
    }

    public onChannelAdded(cb: () => void): void {
        throw new Error("Method not implemented.");
    }

    public onChannelRemoved(cb: () => void): void {
        throw new Error("Method not implemented.");
    }

    public apps(channelId?: string): Promise<any[]> {
        throw new Error("Method not implemented.");
    }

    private handler(context: Glue42.Channels.ChannelContext, updaterId: string): any {
        this.registry.execute(this.subsKey, context.data, context, updaterId);
    }
}
